import tagGenderAge from '../common/tag-gender-age.vue';
export default {
    props:{           
        item:Object,       
    },
    components:{
        tagGenderAge
    },
    data() {
        return {
            isFollow: this.item.isFollow
        }
    },
    methods:{
        follow(){
            this.isFollow = !this.isFollow;
            if(!this.isFollow){
                uni.showToast({
                    title:"取消关注成功",
                })
            }
            else{
                uni.showToast({
                    title:"关注成功",
                })
            }
        },
        //查看大图
        imgdetail(index){
            // current  作为urls的索引
            uni.previewImage({
                current:index,
                urls:this.item.morePic,
                indicator:"number"
            })
        }
    }
}
